video {
    background: url("/public/images/Ghibli-placeholder.png") center center/cover no-repeat; 
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -2;
  }
  
  .hero-container {
    /* background: url("/public/images/Ghibli-placeholder.png") center center/cover no-repeat;  */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }
  
  .hero-container > h1 {
    color: #fff;
    font-size: 70px;
    margin-top: -100px;
    font-family: 'Press Start 2P';
    text-align: center;
  }
  
  .hero-container > p {
    margin-top: 16px;
    color: #fff;
    font-size: 32px;
    font-family: "death-star", 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .hero-btns {
    margin-top: 32px;
  }
  
  .hero-btns .btn {
    padding: 8px 16px;
    margin: 6px;
  }
  
  .fa-caret-down {
    margin: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .hero-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }

  @media screen and (max-width: 450px) {
    .hero-container > h1 {
      font-size: 42px;
      margin-top: -50px;
    }
  
    .hero-container > p {
      font-size: 18px;
    }
    .hero-btns {
      margin-top: 16px;
    }
  }